.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgb(20, 20, 20, 0.8);  
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}

.location{
    
    display: flex;
    margin-bottom: 0.8rem;  
}

.location p {
    line-height: 30px;
}
